.active{
  background-color: rgb(58, 71, 67);
  text-decoration: none;
  font-weight: bold;
  color: rgb(5, 133, 79);
  
}
*{
  width: auto;
  background-color: #1e3932;
  color: white;
}

h1{
  color: white;
}

.coffee img{
  width: 120px;
  margin-right: 10px;
}
.menu img{
  width: 120px;
  margin-right: 10px;
}
.top{
  width: auto;
  position: relative;
  margin: 30px;
}
.button{
  width: 400px;
  position: absolute;
  top: 130px;
}
.button>#coffee{
  padding: 10px 30px;
  margin-right: 15px;
  border: 1px solid white;
}
.button>#menu{
  padding: 10px 30px;
  margin-right: 15px;
  border: 1px solid white;
}
.button>#store{
  padding: 10px 30px;
  margin-right: 15px;
  border: 1px solid white;
}
.top>p{
  position: absolute;
  left: 170px;
  top: -10px;
}
.top>img{
  width: 120px;
}
.top>ul{
  list-style: none;
}
.coffee>ul{
  list-style: none;
}
.coffee{
  position: absolute;
  top: 180px;
}
.menu{
  position: absolute;
  top: 180px;
}
.menu>ul{
  list-style: none;
}
.store{
  position: absolute;
  top: 180px;
}